<template>

<div id="app">

  <div id="page-container">

    <div class="content">
      <Transition name="level">
        <div class="one" v-if="show[0]">
          <div class="credentials" :class="{ 'no-entry': noEntry }">
            <img src="./assets/bouncer.png" class="bouncer" />
            <form @submit.prevent="submit($event)">
              <div class="input-container">
                <input id="first" ref="input-0" type="text" v-model="input" autocomplete="off" autofocus :disabled="noEntry">
                <Transition name="buzz">
                  <div class="wrong" v-if="wrong">‚ùå</div>
                </Transition>
              </div>
            </form>
          </div>
        </div>
      </Transition>
    </div>
  
  </div>

</div>

</template>



<script>

export default {
  name: "App",
  data() {
    return {
      level: 0,
      wow: "you're no fun smh",
      answer: [],
      input: "",
      show: [true, false, false, false, false, false],
      wrong: false,
      timeoutID: "",
      noEntry: true
    }
  },
  watch: {
    level() {
      this.$nextTick(() => {
        setTimeout(() => {
          window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
        }, 1000);
      });
      if (this.answer.length > this.level) {
        this.noEntry = false;
      } else {
        this.noEntry = true;
      }
    }
  },
  methods: {
    change: function () {
      this.wrong = false;
      this.level += 1;
      for (let i = 0; i < this.show.length; i++) {
        this.show[i] = false;
      }
      this.show[this.level] = true;
      if (this.level < 5) {
        this.$nextTick(() => {
          this.$refs[`input-${this.level.toString()}`].focus();
        });
      }
    },
    submit: function (e) {
      e.preventDefault();
      clearTimeout(this.timeoutID);
      // console.log(e.currentTarget[0].id);
      if (this.input.toLowerCase() !== this.answer[this.level]) {
        this.wrong = true;
        this.timeoutID = setTimeout(() => {
          this.wrong = false;
        }, 1250);
      } else {
        clearTimeout(this.timeoutID);
        this.change();
      }
      this.input = "";
    },
    imagePath: function (index) {
      return require(`./assets/bts-${index}.png`);
    },
  },
  mounted() {
  },
}

</script>



<style lang="scss">

* {
  margin: 0;
  padding: 0;
  font-family: "Anonymous Pro", monospace;
  font-size: 100%;
}
*, ::before, ::after {
  box-sizing: border-box;
}

html {
  min-height: 100%;
  width: 100%;
}

body {
  height: 100%;
  width: 100%;
  background-color: black;
}

#app {
  height: 100%;
  width: 100%;
}

#page-container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.content {
  min-height: 100%;
  max-width: 650px;
  color: white;
  font-size: 20px;
  line-height: 1.5em;
  margin: 50px;
}

.credentials {
  padding: 20px 0;
  form {
    @include flex-center;
    .input-container {
      @include flex-center;
      position: relative;
      input {
        padding: 5px 8px;
      }
      input:focus {
        outline: none;
      }
      .wrong {
        position: absolute;
        right: -35px;
      }
    }
  }
}

.bouncer {
  height: 475px;
  img {
    @include image-scale;
  }
}

.one {
  .credentials {
    width: 100%;
    img {
      @include image-scale;
    }
  }
}

.two,
.three,
.four,
.five,
.six {
  @include flex-center;
  flex-direction: column;
  .message {
    @include flex-center;
    flex-direction: column;
    white-space: pre-line;
    margin-bottom: 100px;
    a {
      color: white;
    }
  }
  iframe {
    aspect-ratio: 16 / 9;
    margin-top: 60px;
  }
  .credentials {
    width: 300px;
    .bouncer {
      height: auto;
      width: 100%;
    }
  }
}

.bts-container {
  width: 100%;
  margin-top: 60px;
  .bts-images {
    @include flex-center;
    width: 100%;
    img {
      @include image-scale;
    }
    :nth-child(2) {
      margin: 0 10px;
    }
  }
}

.tiny {
  font-size: 10px;
}
.five .tiny {
  text-align: center;
}

.level-enter-active {
  transition: opacity 2s ease;
}
.level-enter-from,
.level-leave-from {
  opacity: 0;
}

.buzz-enter-active {
  transition: opacity 0.25s ease;
}
.buzz-leave-active {
  transition: opacity 0.75s ease;
}
.buzz-enter-from,
.buzz-leave-to {
  opacity: 0;
}

.no-entry {
  opacity: 0.3;
}

</style>
